<template>
  <div class="register">
    <header class="header">
      <h3>注册</h3>
    </header>
    <div class="frombox" id="captchaForm">
      <div class="up3 up">
        <table>
          <tbody>
            <tr>
              <td>
                <input type="number" name="telephoneNumber" placeholder="请输入手机号" class="phone" maxlength="11" @input="changeInput" ref="myref">
                <p v-if="isExit">已存在，请前往<span @click="deluclick">登录</span>页面</p>
              </td>
            </tr>
            <tr>
              <td>
                <input type="password" name="password" placeholder="请输入密码" class="password" id="password" ref="mypass">
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <input id="submit" type="button" name="submit" value="注册"  @click="myclick">
    </div>
  </div>
</template>

<script type="text/javascript">
  import axios from 'axios';
  export default {
    data(){

      return {
        isExit:false
      }
    },
    mounted(){
      
    },
    methods:{
      changeInput(){
        console.log(this.$refs.myref.value)
        axios({
          url:"/register/checkIt",
          // method:'post',
          data:{
            myusername:this.$refs.myref.value
          }
        }).then(res=>{
          console.log(res.data);
          if(res.data.ok == 0){
            this.isExit=false
          } else{
            this.isExit=true
          }
        })
      },
      deluclick(){
        this.$router.push('/login')
      },
      myclick(){
        axios({
          url:"/register/validate",
          method:'post',
          data:{
            username:this.$refs.myref.value,
            password:this.$refs.mypass.value
          }

        }).then(res=>{
          this.$router.push('/login')
        })
      }
    }
}
</script>

<style scoped lang="scss">
  span{
    color: #f00;
  }

</style>
