<template>
  <div class="film">
    <route-link tag="a" to="cities" activeClass="active" class="map">
        {{mycity}}111
    </route-link>
    <swipe class="my-swipe" ref="myswipe">
      <swipe-item v-for="data in filmslist" :key="data.bannerId" > 
          <img :src="data.imgUrl">
      </swipe-item>
    </swipe>
    <ul :class="isfixed?'swipefix':''">
        <router-link tag="li" activeClass="myactive" to="/film/nowplaying">正在热映</router-link>
        <router-link tag="li" activeClass="myactive" to="/film/comingsoon">即将上映</router-link>
    </ul>
    <router-view></router-view>
  </div>

</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import 'vue-swipe/dist/vue-swipe.css';
import { Swipe, SwipeItem } from 'vue-swipe';
import Vue from "vue";
import {mapState} from 'vuex'
import { Indicator } from 'mint-ui';

Vue.component('swipe', Swipe);
Vue.component('swipe-item', SwipeItem);

export default {
    data(){
        return{
            filmslist:[],
            isfixed:false
        }
    },
    mounted(){
        
        axios({
        url:"https://m.maizuo.com/gateway?type=2&cityId=310100&k=8004404",
        headers:{
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"1.0.0","e":"154807237748816598287839"}',
            'X-Host': 'mall.cfg.common-banner'
        }
        }).then(res=>{
            console.log(res.data);
            this.filmslist=res.data.data;
        })
        window.onscroll = this.handleScroll;
    },
    components:{
        'swipe':Swipe,
        'swipe-item':SwipeItem
    },
    methods:{
        handleScroll(){
            //console.log(this.$refs.myswipe.$el.offsetHeight)
            if((document.documentElement.scrollTop||document.body.scrollTop)>this.$refs.myswipe.$el.offsetHeight){
                this.isfixed = true;
            } else {
                this.isfixed = false;
            }

        }
    }   
};
</script>
<style scoped lang="scss">
    .my-swipe {
      height: 200px;
      img{
        width: 100%;
      }
    }
    ul{
        display: flex;
        border-bottom: 1px solid #eee;
        li{
          flex:1;
          text-align: center;
          height:40px;
          line-height: 40px;
        }
    }
    .myactive{
        border-bottom: 3px solid #f60;
    }
    .swipefix{
        position: fixed;
        left:0;
        top:0;
        width: 100%;
        height: 50px;
        background: #fff;
    }
    .film{
        position: relative;
    }
    .map{
        position:absolute;
        top:20px;
        left:20px;
        z-index: 10;
    }
