<template>
  <div class="nowplaying" >
    <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10" >
        <li v-for="data in datalist" @click="handleClick(data.filmId)" :key="data.filmId">
            <img :src="data.poster">
            <h3>{{data.name}}</h3>
        </li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import infiniteScroll from 'vue-infinite-scroll'
import Vue from 'Vue'
Vue.use(infiniteScroll)
export default {
    data(){
        return{
            datalist:[],
            count:1
        }
    },
    mounted(){
        axios({
            url:"https://m.maizuo.com/gateway?cityId=310100&pageNum=1&pageSize=10&type=1&k=6670054",
            headers:{
                'X-Client-Info': '{"a":"3000","ch":"1002","v":"1.0.0","e":"154807237748816598287839"}',
                'X-Host': 'mall.film-ticket.film.list'
            }
        }).then(res=>{
            console.log(res.data);
            this.datalist=res.data.data.films
        })
        // fetch("https://m.maizuo.com/gateway?cityId=310100&pageNum=1&pageSize=10&type=1&k=6670054",{
        //     headers:{
        //         'X-Client-Info': '{"a":"3000","ch":"1002","v":"1.0.0","e":"154807237748816598287839"}',
        //         'X-Host': 'mall.film-ticket.film.list'
        //     }
        // }).then(res=>res.json()).then(res=>{
        //     console.log(res);
        //     this.datalist=res.data.films
        // })
        
    },
    methods:{
        handleClick(index){
            console.log(index);
            this.$router.push(`/detail/${index}`)
        },
        loadMore: function() {
            this.busy = true;
            this.count++;
            if(this.datalist.length === this.total){
                return ;
            }
            axios({
                url:`https://m.maizuo.com/gateway?cityId=310100&pageNum=${this.count}&pageSize=10&type=1&k=6670054`,
                headers:{
                    'X-Client-Info': '{"a":"3000","ch":"1002","v":"1.0.0","e":"154807237748816598287839"}',
                    'X-Host': 'mall.film-ticket.film.list'
                }
            }).then(res=>{
                console.log(res.data);
                this.datalist=[...this.datalist,...res.data.data.films],
                this.busy = false
            })
            
        }
    }

}
</script>

<style scoped lang="scss">
    li{
        overflow: hidden;
        padding: 10px;
        img{
            float:left;
            width: 150px;
        };
    }
</style>
