<template>
  <div class="login">
    <header class="header">
      <!-- <a href="" class="header-left">
          <img src="../../static/img/index.png">
      </a>
 -->      <h3>登录</h3>
      <!-- <a href="#/register" class="header-right">注册</a> -->
    </header>
    <div class="frombox" id="captchaForm">
      <div class="up up3">
        <input type="text" name="telephoneNumber" placeholder="请输入手机号" class="phone" ref="mytel">
        <div class="huoqu">
            <input type="password" name="password" placeholder="请输入密码" ref="mypas">
            <!-- <input type="button" id="" name="button" value="忘记密码?" placeholder="忘记密码?"> -->
        </div>
        <p v-if="isp">账户和密码不匹配</p>
      </div>
      <input id="submit" type="button" name="submit" value="确认" @click="handleclick">
      <input id="submit" type="button" name="submit" value="注册" @click="handleclick2">

    </div>
  </div>
</template>

<script type="text/javascript">
  import axios from 'axios'
  export default {
    data(){
      return{
        isp:false
      }
    },
    methods: {
      handleclick(){
        axios({
          url:"/login/validate",
          method:'post',
          data:{
            username:this.$refs.mytel.value,
            password:this.$refs.mypas.value
          }

        }).then(res=>{
          console.log(res.data.ok)
          if(res.data.ok == 0){
            this.isp=true;

          } else{
            this.isp = false;
            this.$router.push('/film')
          }
          
        })
      },
      handleclick2(){
        this.$router.push('/register')
      }

    }

  }
</script>

