<template>
    <div>
        <div>
            <h3>热门城市</h3>
            <ul class="ul1map">
                <li v-for="map in maplist" v-if="map.isHot==1">
                    <p @click="handleclick">{{map.name}}</p>
                </li>
            </ul>
        </div>
        <div>
            <h3>城市</h3>
            <ul class="ulmap">
                <li v-for="maps in maplist">
                    <p @click ="handleclick">{{maps.name}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>
<script type="text/javascript">
    import axios from 'axios'
    export default {
        data(){
            return {
                maplist:[]
            }
        },
        mounted(){
            axios({
                url:"https://m.maizuo.com/gateway?k=3624919",
                headers:{
                    'X-Client-Info':'{"a":"3000","ch":"1002","v":"1.0.0","e":"154807237748816598287839"}',
                    'X-Host': 'mall.film-ticket.city.list'
                }
            }).then(res=>{
                console.log(res.data.data.cities)
                this.maplist = res.data.data.cities;

            })
        },
        methods:{
            handleclick(){
                this.$store.commit('changeIndex',index);
                this.$store.commit('changeMyCity',this.maplist[this.cityindex].name);
                this.$store.commit('changeCityId',this.maplist[this.cityindex].cityId);
                this.$store.push('/');
            }
        }
    }
</script>
<style scoped lang="scss">
    ul{
        width: 100%;
        li{
            float: left;
            width:33%;
            height: 40px;
            line-height: 40px;
            text-align: center;
            font-size: 12px;
        }

    }
    h3{
        padding:10px;
    }
    div:after{
        display: block;
        content:"";
        clear:both;
        height: 0;
        width: 100%
    }
</style>