<template>
  <div class="detail" v-if="detailslist">
    <img :src="detailslist.poster">
    <h2>
        {{detailslist.name}}
        <span>{{detailslist.filmType.name}}</span>
        <span><i>{{detailslist.grade}}分</i></span>
    </h2>
    <div class="swiper-container">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="data in detailslist.actors">
                <img :src="data.avatarAddress">
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import Swiper from 'swiper'
import store from '@/store/observe'
import 'swiper/dist/css/swiper.css'

export default {
    data(){
        return{
            detailslist:null
        }
    },
    mounted(){  
        console.log(this.$route.params.myid);      
        axios({
        url:`https://m.maizuo.com/gateway?filmId=${this.$route.params.myid}&k=3201620`,
        headers:{
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"1.0.0","e":"154807237748816598287839"}',
            'X-Host': 'mall.film-ticket.film.info'
        }
        }).then(res=>{
            console.log(res.data);
            this.detailslist=res.data.data.film;
            this.$nextTick(()=>{
                var swiper = new Swiper('.swiper-container', {
                  slidesPerView: 3,
                  spaceBetween: 30,
                });
            })            
        })
        this.$store.commit("changemyshow",false)
    },
    beforeDestory(){
        this.$store.commit("changemyshow",true)
    }
};
</script>
<style scoped lang="scss">
    *{
        padding: 0;
        margin:0;
    }
    html,body{
        touch-action:none;
    }
    img{
        width:100%;
    }
    h2{
        padding-right:10px;
        span:nth-of-type(1){
            font-weight: 100;
            font-size: 15px;

        };
        span:nth-of-type(2){
            font-weight: 100;
            font-size: 20px;
            color:#f60;
            float:right;

        }
    }
    .swiper-slide{
        img{
            width: 100px;
        }
    }
</style>